/*************************************************/
/*    Methods for computing statistics           */
/*************************************************/


/**
 * Compute standard deviation.
 *
 * @param {Float64Array|Array} x - vector with values.
 *
 * @returns {number} standard deviation of x.
 */
export function sd(x) {
   return Math.sqrt(variance(x));
}


/**
 * Compute variance.
 *
 * @param {Float64Array|Array} x - vector with values.
 *
 * @returns {number} variance of x.
 */
export function variance(x) {
   const m = mean(x);
   let s = 0;
   for (let i = 0; i < x.length; i++) {
      const d = (x[i] - m);
      s = s + d * d ;
   }
   return s / (x.length - 1);
}


/**
 * Compute average.
 *
 * @param {Float64Array|Array} x - vector with values.
 *
 * @returns {number} mean of x.
 */
export function mean(x) {
   return sum(x) / x.length;
}


/**
 * Compute sum.
 *
 * @param {Float64Array|Array} x - vector with values.
 *
 * @returns {number} sum of x.
 */
export function sum(x) {
   let s = 0;
   for (let i = 0; i < x.length; i++) {
      s = s + x[i];
   }
   return s;
}


/**
 * Find the smallest element in a sequence of numbers.
 *
 * @param {Array | TypedArrayView} x - Array or typed array with values.
 *
 * @returns {number} the smallest value.
 *
 */
export function min(x) {
   return x[minind(x) - 1];
}


/**
 * Find index of the smallest element in a sequence of numbers.
 *
 * @param {Array | TypedArrayView} x - Array or typed array with values.
 *
 * @returns {number} index of the smallest value (starting from 1).
 *
 */
export function minind(x) {

   let outind = 1;
   let out = x[0]
   for (let i = 2; i <= x.length; i++) {
      if (x[i - 1] < out) {
         outind = i;
         out = x[i - 1];
      }
   }

   return outind;
}


/**
 * Find the largest element in a sequence of numbers.
 *
 * @param {Array | TypedArrayView} x - Array or typed array with values.
 *
 * @returns {number} the largest value.
 *
 */
export function max(x) {
   return x[maxind(x) - 1];
}


/**
 * Find index of the largest element in a sequence of numbers.
 *
 * @param {Array | TypedArrayView} x - Array or typed array with values.
 *
 * @returns {number} index of the largest value (starting from 1).
 *
 */
export function maxind(x) {

   let outind = 1;
   let out = x[0]
   for (let i = 2; i <= x.length; i++) {
      if (x[i - 1] > out) {
         outind = i;
         out = x[i - 1];
      }
   }

   return outind;
}


