<script>
import { Axes, Box, XAxis, YAxis, ScatterSeries} from 'svelte-plots-basic';

export let settings;
export let model;
export let results;

const colors = ["#2679B2", "#D22C2F"];
const markers = [1, 2];

$: console.log(settings)
$: ncomp = settings ? settings.ncomp : 1;
</script>

{#if model !== null && results !== null}
<Axes
   title="{`Distance plot (A = ${ncomp})`}"
   xLabel="Score distance, h/h0"
   yLabel="Orthogonal distance, q/q0">

   {#each results as res, i}
   <ScatterSeries
      xValues={Array.from(res.H.getcolumn(ncomp).map(v => v/model.h0[ncomp - 1]))}
      yValues={Array.from(res.Q.getcolumn(ncomp).map(v => v/model.q0[ncomp - 1]))}
      faceColor = "transparent"
      marker={markers[i]}
      borderColor = {colors[i]}
   />
   {/each}
   <XAxis slot="xaxis" showGrid={true}></XAxis>
   <YAxis slot="yaxis" showGrid={true}></YAxis>
   <Box slot="box"></Box>
</Axes>
{/if}

