/************************************************************/
/*    Methods for Principal Component Analysis              */
/************************************************************/

import { seqind, rep } from './vectors.js';
import { tcrossprod, ncol, nrow, dot, zeros, rowSums } from './Matrix.js';
import { max, mean, sd } from './stat.js';
import { rsvd, svd } from './decomp.js';


export function pcafit(X, ncomp, center, scale) {

   if (center === undefined) {
      center = true;
   }

   if (scale === undefined) {
      scale = false;
   }

   if (!ncomp) {
      ncomp = Math.min(ncol(X), nrow(X) - 1);
   }

   // center and scale the training set
   const mX = center ? X.apply(mean, 2) : rep(0, ncol(X));
   const sX = scale ? X.apply(sd, 2) : rep(1, ncol(X));
   const Xp = X.subtract(mX, 2).divide(sX, 2);

   // compute loadings and eigenvalues
   const m = rsvd(Xp, ncomp);
   const eigenvals = m.s.map(x => x * x / (nrow(X) - 1));

   // compute main PCA results for calibration set
   const calres = pcagetmainres(Xp, m.V, eigenvals);

   // compute mean values for distances
   const h0 = calres.H.apply(mean, 2);
   const q0 = calres.Q.apply(mean, 2);

   // compute degrees of freedom for distances

   // return the model object
   return {
      P: m.V,
      eigenvals: eigenvals,
      center: center,
      scale: scale,
      mX: mX,
      sX: sX,
      q0: q0,
      h0: h0,
      ncomp: ncomp
   }
}

export function pcagetmainres(Xp, P, eigenvals) {

   const ncomp = ncol(P);
   const T = dot(Xp, P);
   const U2 = T.apply(x => x * x, 0).divide(eigenvals, 2);

   let H = zeros(nrow(Xp), ncomp);
   let Q = zeros(nrow(Xp), ncomp);
   let expvar = new Float64Array(ncomp);

   // compute total sum of squares
   let totssq = 0;
   for (let i = 0; i < Xp.length; i++) {
      totssq += Xp.v[i] * Xp.v[i];
   }

   for (let a = 1; a <= ncomp; a++) {
      const Xhat = tcrossprod(T.subset([], seqind(1, a)), P.subset([], seqind(1, a)));
      const E2 = Xp.subtract(Xhat).apply(x => x * x, 0);

      H.v.set(rowSums(U2.subset([], seqind(1, a))), (a - 1) * nrow(Xp));
      Q.v.set(rowSums(E2), (a - 1) * nrow(Xp));

      let resssq = 0;
      for (let i = 0; i < Xp.length; i++) {
         resssq += E2.v[i] * E2.v[i];
      }
      expvar[a - 1] = 1 - resssq / totssq;
   }

   return {
      T: T,
      H: H,
      Q: Q,
      expvar: expvar
   }
}

export function pcapredict(m, X, name) {

   const Xp = X.subtract(m.mX).divide(m.sX);
   let res = pcagetmainres(Xp, m.P, m.eigenvals);
   res.name = name;

   return res;
}
