/************************************************************/
/*    Methods for computing matrix decompositions           */
/************************************************************/

import { norm2, copyvector, dotvv, seqind } from './vectors.js';
import { rand, dot, dotv, nrow, ncol, eye, zeros, Matrix, diagm, crossprod } from './Matrix.js';


export function rsvd(X, ncomp) {

   const m = nrow(X);
   const n = ncol(X);

   if (ncomp === undefined) {
      ncomp = Math.min(m, n);
   }


   if (m < n) {
      const res = rsvd(X.t(), ncomp);
      return {s: res.s, V: res.U, U: res.V}
   }

   // the more the better but slower
   const its = 3;
   const l = 2 * ncomp + 2;

   // // if matrix is small comparing to "l" - do direct SVD
   // if (l >= m / 1.25 || l >= n / 1.25) {
   //    return svd(X, ncomp);
   // }

   let Y = dot(X, rand(n, l, [-1, 1]));
   let Q = qr(Y).Q;

   for (let it = 1; it <= its; it++) {
      Y = crossprod(X, Q);
      Q = qr(Y).Q;
      Y = dot(X, Q);
      Q = qr(Y).Q;
   }

   const res = svd(crossprod(Q, X), ncomp);
   return {s: res.s, V: res.V, U: dot(Q, res.U)};
}

/**
 * QR decomposition
 *
 * @param {Matrix} X - matrix to decompose.
 *
 * @returns {JSON} JSON with two matrices, Q and R, so X = QR.
 */
export function qr(X) {

   const m = nrow(X);
   const n = ncol(X);

   if (m < n) {
      const res = qr(X.subset([], seqind(1, m)));
      return {Q:res.Q, R:crossprod(res.Q, X)}
   }

   let Qt = eye(m)
   let Rt = X.t();

   for (let j = 1; j <= n; j++) {
      for (let i = m; i >= (j + 1); i--) {

         const r1 = Rt.getcolumn(i - 1);
         const r2 = Rt.getcolumn(i);

         const q1 = Qt.getcolumn(i - 1);
         const q2 = Qt.getcolumn(i);

         const r1c = copyvector(r1);
         const r2c = copyvector(r2);

         const q1c = copyvector(q1);
         const q2c = copyvector(q2);

         const [c, s, r] = rot(r1c[j- 1], r2c[j- 1]);

         // apply transformation to columns i and i - 1 of matrix Rt
         for (let i = 0; i < n; i++) {
            r1[i] = r1c[i] * c + r2c[i] * s;
            r2[i] = r1c[i] * -s + r2c[i] * c;

            q1[i] = q1c[i] * c + q2c[i] * s;
            q2[i] = q1c[i] * -s + q2c[i] * c;
         }

         for (let i = n; i < m; i++) {
            q1[i] = q1c[i] * c + q2c[i] * s;
            q2[i] = q1c[i] * -s + q2c[i] * c;
         }
      }
   }

   return {
      Q: m === n ? Qt : Qt.subset([], seqind(1, n)),
      R: m === n ? Rt.t() : Rt.t().subset(seqind(1, n), [])
   }
}


/**
 * LU decomposition.
 *
 * @param {Matrix} X - matrix to decompose.
 *
 * @returns {JSON} JSON with two matrices, L and U, so X = LU.
 */
export function lu(X) {

   const n = nrow(X);
   const Lv = new Float64Array(n * n);
   const Uv = new Float64Array(n * n);
   const Xv = X.v.slice();

   const s = Math.round(Math.pow(n, 0.5285425));
   let z = 1;
   for (let c = 1; c <= n; c++) {

      if (c == z + s) {

         const no = n - c + 1;
         const ni = c - z;
         const Sv = new Float64Array(no * no);

         for (let i = 0; i < no; i++) {
            for (let j = 0; j < no; j++) {
               let s = 0;
               for (let k = 0; k < ni; k++) {
                  s += Lv[(z - 1 + k) * n + (c - 1) + i] * Uv[(c - 1 + j) * n + (z - 1 + k)];
               }
               Sv[j * no + i] = s;
            }
         }

         for (let i = c; i <= n; i++) {
            for (let j = c; j <= n; j++) {
               Xv[(j - 1) * n + (i - 1)] = Xv[(j - 1) * n + (i - 1)] - Sv[(j - c) * no + (i - c)];
            }
         }

         z = c;
      }

      for (let i = c; i <= n; i++) {
         let acc = Xv[(c - 1) * n + (i - 1)];
         for (let k = z; k <= c - 1; k++) {
            acc = acc - Lv[(k - 1) * n + (i - 1)] * Uv[(c - 1) * n + (k - 1)];
         }
         Lv[(c - 1) * n + (i - 1)] = acc;
      }

      for (let i = c; i <= n; i++) {
         let acc = Xv[(i - 1) * n + (c - 1)];
         for (let k = z; k <= c - 1; k++) {
            acc = acc - Lv[(k - 1) * n + (c - 1)] * Uv[(i - 1) * n + (k - 1)];
         }
         Uv[(i - 1) * n + (c - 1)] = acc / Lv[(c - 1) * n + (c - 1)];
      }
   }

   return {L: new Matrix(Lv, n, n), U: new Matrix(Uv, n, n)};
}


/**
 * Singular value decomposition
 *
 * @param {Matrix} X - matrix to decompose.
 * @param {number} ncomp - number of components.
 *
 * @return {JSON} JSON with two fields, S with singular values and V with right singular vectors matrix.
 */
export function svd(X, ncomp) {

   if (nrow(X) < ncol(X)) {
      const res = svd(X.t(), ncomp);
      return {s: res.s, V: res.U, U: res.V}
   }

   const m = nrow(X);
   const n = ncol(X);

   if (!ncomp) {
      ncomp = Math.min(m, n);
   }

   let [B, V, U] = bidiag(X);

   // get diagonal elements of B
   let d = new Float64Array(n);
   let e = new Float64Array(n - 1);
   for (let i = 0; i < n - 1; i++) {
      d[i] = B.v[i * n + i];
      e[i] = B.v[(i + 1) * n + i];
   }
   d[n - 1] = B.v[(n - 1) * n + n - 1];

   const maxit = 500 * n**2;
   const thresh = Math.pow(10, -16);
   let Gt = eye(n);
   let P = eye(n);

   for (let it = 0; it < maxit; it++) {

      // find first nonzero element in e from bottom
      let iU = 0;
      for (let i = n - 1; i >= 1; i--) {
         if (Math.abs(e[i - 1]) > thresh) {
            iU = i
            break;
         }
      }

      // find first nonzero element in e from top
      let iL = iU + 1;
      for (let i = 1; i <= n - 1; i++) {
         if (Math.abs(e[i - 1]) > thresh) {
            iL = i;
            break;
         }
      }

      // check the convergence and return result
      if ((iU == iL && Math.abs(e[iU - 1]) <= thresh) || (iU < iL)) {

         const s = d.subarray(0, ncomp);
         const S = diagm(s.map(v => Math.sign(v)));
         return {
            s: s.map(v => Math.abs(v)),
            U: dot(dot(U, P).subset([], seqind(1, ncomp)), S),
            V: dot(V, Gt).subset([], seqind(1, ncomp))
         };

      }

      // re-sweep
      const [rd, re, rG, rPt] = vsweep(d.subarray(iL - 1, iU + 1), e.subarray(iL - 1, iU));

      // replace elements of d and e
      d.set(rd, iL - 1);
      e.set(re, iL - 1);


      // now we need to compute:
      // G = rG' * G = (G' * rG)' -> this should be done only fo selected columns in G
      // P =            P  * rP'  -> only for selected columns in P

      // get local copy of selected columns from G' and P
      const l = iU - iL + 2;
      const lGt = zeros(n, l);
      const lP = zeros(n, l)
      for (let c = iL; c <= iU + 1; c++) {
         lGt.v.set(Gt.v.subarray((c - 1) * n, c * n), (c - iL) * n);
         lP.v.set(P.v.subarray((c - 1) * n, c * n), (c - iL) * n);
      }

      // compute dot(lG', rG) and save back to G
      // compute dot(P, rP') and save back to P
      for (let c = 1; c <= l; c++) {
         const newgc = dotv(lGt, rG.getcolumn(c));
         const newpc = dotv(lP, rPt.getcolumn(c));
         Gt.v.set(newgc.v, (c - 1 + iL - 1) * n);
         P.v.set(newpc.v, (c - 1 + iL - 1) * n);
      }
   }

   throw Error("svd: can not converge.")
}


/**
 * Applies givens rotations to diagonal elements of bidiagonalized matrix.
 *
 * @param {Float64Array} d - vector with main diagonal elements.
 * @param {Float64Array} e - vector with diagonal elements on top of main diagonal.
 *
 * @returns {Array} array with vectors 'd' and 'e' after rotation as well as right
 * transformation matrix G.
 */
export function vsweep(d, e) {

   const n = d.length;

   let cold = 1;
   let sold = 1;

   let c = 1;
   let s = 0;
   let r = 0;

   let G = eye(n);
   let Pt = eye(n);

   for (let k = 1; k <= (n - 1); k++) {
      [c, s, r] = rot(c * d[k - 1], e[k - 1]);

      const gk = G.getcolumn(k);
      const gkc = gk.slice();

      const gkp1 = G.getcolumn(k + 1);
      const gkp1c = gkp1.slice();

      for (let i = 0; i < n; i++) {
         gk[i] = gkc[i] * c + gkp1c[i] * s;
         gkp1[i] = -gkc[i] * s + gkp1c[i] * c;
      }

      if (k != 1) {
         e[k - 2] = r * sold;
      }

      [cold, sold, r] = rot(cold * r, d[k] * s);
      d[k - 1] = r;

      const ptk = Pt.getcolumn(k);
      const ptkc = copyvector(ptk);
      const ptkp1 = Pt.getcolumn(k + 1);
      const ptkp1c = copyvector(ptkp1);

      for (let i = 0; i < n; i++) {
         ptk[i] = ptkc[i] * cold + ptkp1c[i] * sold;
         ptkp1[i] = -ptkc[i] * sold + ptkp1c[i] * cold;
      }
   }

   const h = c * d[n - 1];
   e[n - 2] = h * sold;
   d[n - 1] = h * cold;

   return [d, e, G, Pt];
}


/**
 * Generate c, s, r values for Givens rotations, so [c s; -s c][f; g] = [r; 0].
 *
 * @param {number} f - first element of vector.
 * @param {number} g - second element of vector.
 * @return array with values [c, s, r].
 */
export function rot(f, g) {

   if (f == 0) {
      return [0, 1, g];
   }

   if (Math.abs(f) > Math.abs(g)) {
      const t = g / f;
      const t1 = Math.sqrt(1 + t * t);
      const c = 1 / t1
      return [c, t * c, f * t1];
   }

   const t = f / g;
   const t1 = Math.sqrt(1 + t * t)
   const s = 1 / t1
   return [t * s, 1 / t1, g * t1];
}


/**
 * Golub-Reinsch bidiagonalization of matrix A
 *
 * @param {Matrix} A - matrix with values.
 *
 * @returns {Matrix} bidiagonalized version of A, plus right transformation matrix, V.
 */
export function bidiag(A) {

   const m = nrow(A);
   const n = ncol(A);

   if (m < n) {
      throw Error("bidiag: number of rows in A must not be smaller number of columns.");
   }

   let Ut = eye(m);
   let V = eye(n);
   let B = A.copy();

   for (let k = 1; k <= (m > n ? n : n - 1); k++) {

      const mk = m - k + 1;

      // compute:
      // B = H %*% B
      // U' = H' %*% U';

      const H1 = householder(B.getcolumn(k), k);
      for (let c = 1; c <= n; c++) {

         // get c-th column of B and make a copy
         const bc = B.getcolumn(c);
         const bcc = copyvector(bc);

         // get c-th column of U' and make a copy
         const utc = Ut.getcolumn(c);
         const utcc = copyvector(utc);

         for (let r = k; r <= m; r++) {
            // because H is symmetric we take column instead of row
            const hr = H1.getcolumn(r - k + 1);
            let sb = 0;
            let sut = 0;
            for (let i = 0; i < mk; i++) {
               sb += hr[i] * bcc[k + i - 1];
               sut += hr[i] * utcc[k + i - 1]
            }
            bc[r - 1] = sb;
            utc[r - 1] = sut;
         }
      }


      for (let c = n + 1; c <= m; c++) {

         // get c-th column of U' and make a copy
         const utc = Ut.getcolumn(c);
         const utcc = copyvector(utc);

         for (let r = k; r <= m; r++) {
            const hr = H1.getcolumn(r - k + 1);
            let sut = 0;
            for (let i = 0; i < mk; i++) {
               sut += hr[i] * utcc[k + i - 1];
            }
            utc[r - 1] = sut;
         }
      }

      if (k < n - 1) {

         // compute:
         // B = B %*% t(H)
         // V = V %*% H
         const H2 = householder(B.getrow(k), k + 1);
         for (let r = 1; r <= n; r++) {
            const br = B.getrow(r);
            const vr = V.getrow(r);
            for (let c = k + 1; c <= n; c++) {
               const hc = H2.getcolumn(c - k);
               let sb = 0;
               let sv = 0;
               for (let i = 0; i < nrow(H2); i++) {
                  sb += br[k + i] * hc[i];
                  sv += vr[k + i] * hc[i];
               }
               B.v[(c - 1) * m + (r - 1)] = sb;
               V.v[(c - 1) * n + (r - 1)] = sv
            }
         }

         for (let r = n + 1; r <= m; r++) {
            const br = B.getrow(r);
            for (let c = k + 1; c <= n; c++) {
               const hc = H2.getcolumn(c - k);
               let sb = 0;
               for (let i = 0; i < nrow(H2); i++) {
                  sb += br[i + k] * hc[i];
               }
               B.v[(c - 1) * m + (r - 1)] = sb;
            }
         }
      }
   }

   return [B.subset(seqind(1, n), []), V, new Matrix(Ut.t().v.subarray(0, n * m), m, n)];
   // return [B, V, Ut.t()];
}


/**
 * Compute elements of Householder transformation as a matrix.
 *
 * @param {Float64Array|Array} b - vector with diagonal values.
 * @param {number} k - position to start with.
 *
 * @returns {Matrix} matrix with transformation values.
 */
export function householder(b, k) {

   // get vector with values
   const h = householderv(b, k);
   const n = h.length;

   // compute matrix as outer product of the vector
   const H = zeros(n);
   for (let c = 0; c < n; c++) {
      for (let r = 0; r < n; r++) {
         H.v[c  * n + r] = 1 * (r == c) - 2 * h[r] * h[c];
      }
   }

   return H;
}


/**
 * Compute elements of Householder transformation as a vector.
 *
 * @param {Float64Array|Array} b - vector with diagonal values.
 * @param {number} k - position to start with.
 *
 * @returns {Float64Array} vector with transformation values.
 */
export function householderv(b, k) {

   if (b.constructor !== Float64Array) {
      b = new Float64Array(b);
   }
   const n = b.length;

   if (k >= n) {
      throw Error("householder: parameter 'k' must be smaller than length of vector 'b'.")
   }

   const hlen = n - k + 1;
   const h = copyvector(b, k, hlen);
   h[0] = h[0] - Math.sign(h[0]) * norm2(h);
   const hnorm = norm2(h);
   for (let i = 0; i < hlen; i++) {
      h[i] = h[i] / hnorm;
   }
   return h;
}





