<script>
   import { pcapredict } from './mdatools/pca.js';

   import DataLoader from './DataLoader.svelte';
   import PCVCalculator from './PCVCalculator.svelte';
   import PCADistancePlot from './mdatools-plots/PCADistancePlot.svelte';
   import PCACalculator from './PCACalculator.svelte';
   import PCADistancePlotControls from './mdatools-plots/PCADistancePlotControls.svelte';

   let dataset = null;
   let pvset = null;
   let pcamodel = null;
   let plotsettings = null;

   $: pcares = (dataset !== null && pvset !== null && pcamodel !== null) ?
         [pcapredict(pcamodel, dataset.values, "cal"), pcapredict(pcamodel, pvset, "pv")] : null;

   $: if (!pvset) plotsettings = null;
</script>

<div class="app-container">
   <div class="app-controls">
      <div class="data-loader">
         <DataLoader bind:dataset={dataset}  />
      </div>
      <div class="pcv-calculator">
         <PCACalculator {dataset} bind:pcamodel={pcamodel} />
      </div>
      <div class="pcv-calculator">
         <PCVCalculator {dataset} {pcamodel} bind:pvset={pvset} />
      </div>
      {#if pcamodel && pvset}
      <PCADistancePlotControls model={pcamodel} bind:settings={plotsettings} />
      {/if}
   </div>
   <div class="app-content">

      {#if pvset === null }
      <hgroup>
         <h1>Procrustes cross-validation</h1>
         <p>create a validation set based on your calibration data.</p>
      </hgroup>
      {/if}

      {#if dataset === null}
      <p>
         <strong>Step 1.</strong><br>
         Load your calibration set from a CSV file. The file should contain numeric values delimited
         by comma (with dot as decimal separator) or by semicolon (with comma as decimal separator). Rows correspond to observations or objects, columns — to variables. First row can content variable names (header) — app will detect this automatically.
      </p>
      {:else}

         {#if pcamodel === null}
         <p>
            <strong>Step 2</strong><br>
            Next step is to create a global PCA model for the training set. Use as many components as you need, there is no overfitting in PCV.
         </p>
         {/if}


         {#if pcamodel !== null && pvset === null}
         <p>
            <strong>Step 3</strong><br>
            Now set necessary parameters for Procrustes cross-validation method and click button to compute the PV-set. It is recommended to use fewer number of segments (e.g. 3 or 4). The more segment you use the more time it will take to compute the PV-set.
         </p>
         {/if}
      {/if}

      {#if pvset && pcares}
      {#key pcares}
      <PCADistancePlot model={pcamodel} results={pcares} settings={plotsettings}/>
      {/key}
      {/if}
   </div>
</div>

