<script>
import { nrow, ncol } from './mdatools/Matrix.js';
import { pcvpca } from './mdatools/pcv.js';

import Select from './controls/Select.svelte';
import ControlElementContainer from './controls/Container.svelte';
import ButtonCancel from './controls/ButtonCancel.svelte';
import Button from './controls/Button.svelte';
import Spinner from './controls/Spinner.svelte';

export let dataset;
export let pcamodel;
export let pvset = null

let nseg = 4;
let cvType = "ven";
let pcvpromise;

// constant options
const cvtypeOptions = ["ven", "rand", "loo"];

function downloadPVSet(e) {
   const blob = new Blob([pvset.toCSV(dataset.sep, dataset.dec, dataset.header)], { type: 'text/csv' });
   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', 'pvset.csv');
   a.click()
}

function resetPVSet() {
   msg = null;
   errMsg = null;
   pvset = null;
   pcvpromise = null;
}


function getPVSet(data, model, cv) {
   return new Promise((resolve, reject) => {
      setTimeout(() => {
         resolve(pcvpca(data, model, model.ncomp, cv));
      }, 100);
   });
}

async function computePVSet(data, model, cv) {
   pcvpromise = getPVSet(data, model, cv)
      .then((value) => {
         pvset = value;
      })
}

function compute(e) {
   computePVSet(dataset.values, pcamodel, {type: cvType, nseg: nseg});
}

function reset() {
   pcvpromise = null;
   pvset = null;
}

// if data file is empty reset status and error messages
$: if (pcamodel === null) reset();


// adjust options for number of CV segments based on dataset size
$: nsegOptions = dataset && nrow(dataset.values) > 0 ?
   [2, 3, 4, 5, 10, 20].filter(v => v <= nrow(dataset.values) / 2 ) : [];

</script>

{#if pcvpromise}

{#await pcvpromise}
   <ControlElementContainer>
      <Spinner />
      <p>Computing PV-set...</p>
   </ControlElementContainer>
{:then value}
   {#if pvset !== null}
   <ControlElementContainer>
      <p>PV-set is computed: {nrow(pvset)} &times; {ncol(pvset)}</p>
      <ButtonCancel on:click={reset} />
   </ControlElementContainer>
   <ControlElementContainer label=" ">
      <Button on:click={downloadPVSet}>Download</Button>
   </ControlElementContainer>
   {/if}

{:catch error}
   <ControlElementContainer status="error">
      <p>{error}</p>
   </ControlElementContainer>
{/await}

{:else}

   {#if pcamodel && !pvset}
   <ControlElementContainer label="CV split type:">
      <Select id="cvType" bind:value={cvType} options={cvtypeOptions} />
   </ControlElementContainer>

   {#if cvType !== "loo"}
   <ControlElementContainer label="CV segments:">
      <Select id="nseg" bind:value={nseg} options={nsegOptions} />
   </ControlElementContainer>
   {/if}

   <ControlElementContainer label=" ">
      <Button on:click={compute}>Compute PV-set</Button>
   </ControlElementContainer>
   {/if}

{/if}





