export function ssq(x) {
   let ssq = 0;
   for (let i = 0; i < x.length; i++) {
      ssq += x[i] * x[i];
   }
   return ssq;
}


/**
 * Compute norm2 of a vector (Euclidean distance).
 *
 * @param {Float64Array|Array} x - vector with values.
 *
 * @returns {number} norm.
 */
export function norm2(x) {
   return Math.sqrt(ssq(x));
}


export function dotvv(a, b) {
   let out = 0.0;
   for (let i = 0; i < a.length; i++) {
      out += a[i] * b[i];
   }
   return out;
}


export function addvv(a, b, alfa, beta) {

   if (alfa === undefined) {
      alfa = 1.0;
   }

   if (beta === undefined) {
      beta = 1.0;
   }

   const n = a.length;
   const out = new Float64Array(n);
   for (let i = 0; i < n; i++) {
      a[i] = alfa * a[i] + beta * b[i];
   }
}

/**
 * Generate sequence of indices - a vector with integer values between 'start' and 'end'.
 *
 * @param {number} start - first index.
 * @param {number} end - last index.
 * @param {number} by - increment between indices.
 *
 * @returns {Int32Array} vector with indices.
 *
 * @description All arguments must be integer and (end - start + 1) / by must be a whole number.
 *
 */
export function seqind(start, end, by) {

   if (by === undefined) by = (start <= end) ? 1 : -1;

   const length = Math.floor((end - start) / by) + 1;

   if (length < 1) {
      throw Error('seqind: wrong combination of parameters.');
   }

   const X = new Int32Array(length);
   for (let i = 0; i < length; i++) {
      X[i] = start + i * by;
   }

   return X;
}


/**
 * Replicate a number or a vector of numbers 'n' times.
 *
 * @param {number | number[]} x - a number of a vector of numbers to replicate.
 * @param {number} n - how many times 'x' must be replicated.
 *
 * @returns {Float64Array} a vector with replicated 'x' values.
 *
 */
export function rep(x, n) {

   if (n < 1) {
      throw Error('rep: parameter "n" must be an integer number > 1.');
   }

   if (typeof(x) === 'number') {
      const out = new Float64Array(n);
      for (let i = 1; i <= n; i++) {
         out[i - 1] = x;
      }
      return out;
   }

   if (x.length === undefined || x.length < 1) {
      throw Error('rep: parameter "x" must be a number or a vector with numbers.');
   }

   const l = x.length * n;
   const out = new x.constructor(l);
   for (let i = 1; i <= n; i++) {
      for (let j = 0; j < x.length; j++) {
         out[(i - 1) * x.length + j] = x[j];
      }
   }

   return out;
}


/**
 * Replicate each value in a vector 'x' 'n' times.
 *
 * @param {number[]} x - a vector of numbers to replicate.
 * @param {number} n - how many times each value in 'x' must be replicated.
 *
 * @returns {Float64Array} a vector with replicated values.
 *
 */
export function repeach(x, n) {


   if (n < 1) {
      throw Error('rep: parameter "n" must be an integer number > 1.');
   }

   if (typeof(x) === 'number') {
      return rep(x, n);
   }

   if (x.length === undefined || x.length < 1) {
      throw Error('rep: parameter "x" must be a vector with numbers.');
   }

   const l = x.length * n;
   const out = new x.constructor(l);
   for (let i = 0; i < x.length; i++) {
      for (let j = 1; j <= n; j++) {
         out[i * n + j - 1] = x[i];
      }
   }

   return out;
}


/**
 * Copy whole or a part of typed array (vector).
 *
 * @param {TypedArrayView} src - any typed array (e.g. Float64Array).
 * @param {number} from - which index starts from (default 1).
 * @param {number} length - how many values to copy (default - all).
 *
 * @returns {TypedArrayView} a copy of the typed array of the same type.
 *
 */
export function copyvector(src, from, length)  {

   if (from === undefined) {
      from = 1;
   }

   if (length === undefined) {
      length = src.length - (from - 1);
   }

   return src.slice(from - 1, from + length - 1)

   // const constructor = src.constructor;
   // const elsize = constructor.BYTES_PER_ELEMENT

   // return new constructor(copybuffer(src.buffer, src.byteOffset + (from - 1) * elsize, length * elsize));
}


/**
 * Copy whole or a part of array buffer for any typed array.
 *
 * @param {ArrayBuffer} src - array buffer to take a copy of.
 * @param {number} from - which byte starts from (default 0).
 * @param {number} length - how many bytes to copy (default - all).
 *
 * @returns {ArrayBuffer} a copy of the buffer.
 *
 */
export function copybuffer(src, from, length)  {

   if (from === undefined) {
      from = 0;
   }

   if (length === undefined) {
      length = src.byteLength - from;
   }

   let dst = new ArrayBuffer(length);
   new Uint8Array(dst).set(new Uint8Array(src, from, length));
   return dst;
}
