<script>
import { pcafit } from './mdatools/pca.js';
import { nrow, ncol } from './mdatools/Matrix.js';

import ControlElementContainer from './controls/Container.svelte';
import ButtonCancel from './controls/ButtonCancel.svelte';
import Button from './controls/Button.svelte';
import Range from './controls/Range.svelte';
import Select from './controls/Select.svelte';
import Spinner from './controls/Spinner.svelte';

export let dataset = null;
export let pcamodel = null;

let pcapromise;

let ncomp;
let center = "yes";
let scale = "no"


function getPCAModel(data, ncomp, center, scale) {
   return new Promise((resolve, reject) => {
      setTimeout(() => {
         resolve(pcafit(data, ncomp, center, scale));
      }, 100);
   });
}

async function computePCAModel(data, ncomp, center, scale) {
   pcapromise = getPCAModel(data, ncomp, center, scale)
      .then((value) => {
         pcamodel = value;
      })
}

function compute(e) {
   computePCAModel(dataset.values, ncomp, center === "yes", scale === "yes");
}

function reset() {
   pcapromise = null;
   pcamodel = null;
}

// if data file is empty reset status and error messages
$: if (dataset === null) reset();

$: maxNComp = dataset ? Math.min(nrow(dataset.values) - 1, ncol(dataset.values)) : null;
$: ncomp = maxNComp ? Math.min(maxNComp, 20) : null;

</script>


{#if pcapromise}

{#await pcapromise}

<ControlElementContainer>
   <Spinner />
   <p>Computing PCA model...</p>
</ControlElementContainer>

{:then result}

{#if pcamodel !== null}
<ControlElementContainer>
   <p>Global model is computed: {pcamodel.ncomp} PCs</p>
   <ButtonCancel on:click={reset} />
</ControlElementContainer>
{/if}

{:catch error}

<ControlElementContainer status="error">
   <p>{error}</p>
</ControlElementContainer>

{/await}

{:else}

{#if dataset && !pcamodel}
<ControlElementContainer label="# of PCs">
   <Range id="ncomp" bind:value={ncomp} min={1} max={maxNComp} step={1} decNum={0} />
</ControlElementContainer>
<ControlElementContainer label="Center">
   <Select id="center" bind:value={center} options={["yes", "no"]} />
</ControlElementContainer>
<ControlElementContainer label="Scale">
   <Select id="ncom" bind:value={scale} options={["yes", "no"]} />
</ControlElementContainer>
<ControlElementContainer label=" ">
   <Button on:click={compute}>Create PCA model</Button>
</ControlElementContainer>
{/if}

{/if}


<style>
</style>